.completed{
    text-decoration: line-through;
    color: white;
}
.todo-form p{
    cursor:pointer;
}
.todo-content{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100vw;
    overflow: hidden;
    background-color: #282c34;
}
.todo-content h1{
    color: white;
    text-shadow: 2px 2px 0 purple;
}
.todo-list{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 55%;
    margin: 15px;
}
.todo-form,.todo-edit{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 350px;
    margin: 15px;
}
.todo-form:nth-child(n){
    background: lime;
    border-radius: 20px;
    padding: 0 12px;
    box-shadow: 0 0 15px lime;
}
.todo-form:nth-child(2n){
    background: cyan;
    border-radius: 20px;
    padding: 0 12px;
    box-shadow: 0 0 15px cyan;
}
.todo-form:nth-child(3n){
    background: tomato;
    border-radius: 20px;
    padding: 0 12px;
    box-shadow: 0 0 15px tomato;
}
.todo-list input,.todo-edit input,.search{
    border-radius: 12px 0;
    outline: 0;
    width: 80%;
    box-shadow: 0 0 15px mediumaquamarine;
    border: 0;
    padding: 12px 20px;
    text-indent: 12px;
    background:mediumseagreen;
    color: white;
}
.todo-list input:focus,.todo-edit input:focus,.search:focus{
    border-radius: 12px 0;
    padding: 8px 16px;
    text-indent: 12px;
    outline: 2px solid lightsalmon;
}
input::placeholder{
    color: white;
}
.btn{
    background: inherit;
    color: white;
    border-radius: 12px 0;
    height: 40px;
    padding: 0 12px;
    background: darkslategrey;
    border: 0;
    box-shadow: 0 0 10px darkgrey;
    cursor:pointer;
}
.todo-item{
    width:100%;
    display:flex;
    align-items:center;
    justify-content:space-evenly;
}

.buttons{
    width:45%;
    display:flex;
    align-items:center;
    justify-content:space-evenly;
}
@media only screen and (max-width:768px){
    .todo-item{
        display:flex;
        flex-direction:column;
        align-items:center;
        justify-content:center;
    }
    .todo-list{
        width:100%;
        display:flex;
        align-items:center;
        justify-content:space-evenly;
    }
    .buttons{
        margin:35px 0;
        width:100%;
        display:flex;
        align-items:center;
        justify-content:space-evenly;
    }
}